import React from 'react'
import { connect } from 'react-redux'
import { Link } from 'gatsby'
import cx from 'classnames'

import helpers from '../../../../helpers'
import * as actions from '../../../../redux/actions'

import styles from '../Aside.module.scss'

const { LinksFooter } = helpers.constants.links
const { Exit: ExitModalName } = helpers.constants.modals

function Footer({ currentPage, openModal }) {
    return (
        <footer className={styles.footer}>
            {LinksFooter.map(l => (
                <Link
                    to={l.link}
                    className={cx([
                        styles.navLink,
                        {
                            [styles.navLink_active]: l.link === currentPage,
                        },
                    ])}
                    key={l.text}
                    onClick={e => l.onClick(e, ExitModalName, openModal)}
                >
                    <img src={l.icon} alt={l.text} />
                    <span>{l.text}</span>
                </Link>
            ))}
        </footer>
    )
}

const ConnectedFooter = connect(
    null,
    dispatch => ({
        openModal: name => dispatch(actions.ModalsActions.default.openModal(name)),
    }),
)(Footer)

export default ConnectedFooter
